.calendar {
    display: flex;
    justify-content: center;
    width: 80%;
    margin: auto;
    margin-bottom: 20px;
}
.calendar>div {
    margin-top: 20px;
    text-align: center;
}
.switcher {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    width: 11%;
    max-width: 60px;
    background-color: #000;
    color: #000;
    text-transform: uppercase;
    font-size: 14px;
    cursor: pointer;
    transition: 0.4s;
}
.switcher:hover {
    color: #fff;
}
.switcher:first-of-type {
    padding-left: 3px;
    border-top-left-radius: 50%;
    border-bottom-left-radius: 50%;
    border-right: 2px solid #FF6D12;
}
.switcher:last-of-type {
    padding-right: 3px;
    border-top-right-radius: 50%;
    border-bottom-right-radius: 50%;
    border-left: 2px solid #FF6D12;
}

.day {
    box-sizing: border-box;
    width: 28%;
    padding: 10px 20px;
    border: #000 1px solid;
    background-color: #000;
    color: #fff;
    transition: box-shadow 0.4s;
}
.day:hover {
    cursor: pointer;
    box-shadow: 0px 15px 15px -10px #FF6D12;
}
.day p {
    padding: 0;
}
.day p:first-child {
    font-size: 1em;
    margin: 0;
}
.day p:last-child {
    font-size: 0.7em;
    margin: 0;
}
.selected {
    color: #000;
    background-color: #FF6D12;
    border-bottom: none;
    box-shadow: 0px 0px 20px 2px #000 inset;
}
.day.selected:hover {
    cursor: default;
    color: #000;
    background-color: #FF6D12;
    box-shadow: 0px 0px 20px 2px #000 inset;
}

.movie {
    display: flex;
    max-width: 540px;
    margin: 40px 15% 0;
    background-color: #000;
    color: #fff;
    box-shadow: 0px 0px 15px -8px #fff;
}
.movie .poster-wrapper {
    box-sizing: border-box;
    width: 32%;
    padding: 4px;
}
.movie .poster-wrapper img {
    width: 100%;
    opacity: 0.8;
}
.movie .info-wrapper {
    box-sizing: border-box;
    width: 68%;
    padding: 10px;
    padding-left: 15px;
}
.info-wrapper .short-description {
    margin-bottom: 20px;
}
.info-wrapper .short-description strong {
    display: block;
    margin-bottom: 12px;
    font-size: 1.2em;
}
.info-wrapper .short-description p {
    margin: 0;
    font-size: 0.9em;
}
.info-wrapper .short-description p span {
    color: #999;
}
.info-wrapper .sessions {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 20px;
}
.info-wrapper .sessions span, .info-wrapper .sessions a {
    display: inline-block;
    width: 44px;
    margin: 4px;
    padding: 3px 6px;
    border-radius: 8px;
    color: #FF6D12;
    text-align: center;
}
.info-wrapper .sessions span {
    text-decoration: line-through;
    color: #777;
    cursor: default;
    border-bottom: 2px solid #777;
}
.info-wrapper .sessions a {
    text-decoration: none;
    border-bottom: 2px solid #FF6D12;
    transition: 0.3s;
}
.info-wrapper .sessions a:hover {
    color: #fff;
    border-color: #fff;
}
.movie .info-wrapper>a {
    text-decoration: none;
    color: #999;
    font-size: 0.9em;
    transition: 0.3s;
}
.movie .info-wrapper>a:hover {
    text-decoration: underline;
}

@media (max-width: 800px) {
    .calendar {
        width: 100%;
    }
}
@media (max-width: 780px) {
    .movie {
        margin: 40px 12% 0;
    }
}
@media (max-width: 700px) {
    .movie {
        margin: 40px 8% 0;
    }
}
@media (max-width: 640px) {
    .movie {
        margin: 40px 4% 0;
    }
}
@media (max-width: 600px) {
    .movie {
        margin: 20px 2% 0;
    }
    .day p:first-child {
        font-size: 0.7em;
    }
    .day p:last-child {
        font-size: 0.5em;
    }
    .day {
        padding: 10px 12px;
    }
    .switcher {
        font-size: 0.7em;
    }
}
@media (max-width: 500px) {
    .movie {
        margin: 20px 0 0;
    }
}